<template>
  <div>
    <div class="install" v-if="!installed">
      <div>
        <template v-if="browser.includes('Chrome')">
          <p>Install App</p>
          <button type="button" class="button" @click="installPWA">
            Install!
          </button>
        </template>
        <template v-else>
          Installation instruction for {{browser}}
        </template>

      </div>

    </div>
    <template v-else>
      <div class="top-bar">
        <div class="top-bar-left">
          <ul ref="dropDown" class="dropdown menu" data-dropdown-menu>
            <li class="menu-text">Документы</li>
          </ul>
        </div>
      </div>
      <div class="expanded row" id="content">
        <div class="columns small-12">
          <router-view></router-view>
        </div>
      </div>
    </template>
  </div>
</template>

<script>
export default {
  data() {
    return {
      installed: false,
      installEvent: null
    }
  },
  beforeMount() {
    window.addEventListener('beforeinstallprompt', (e) => {
      console.log(333, e)
      e.preventDefault()
      this.installEvent = e
    })
    if (window.matchMedia('(display-mode: standalone)').matches) {
      this.installed = true
    } else {
      this.installed = false

    }
  },
  computed: {
    browser() {
      let sBrowser, sUsrAg = navigator.userAgent;


      if (sUsrAg.indexOf("Firefox") > -1) {
        sBrowser = "Firefox";
        //"Mozilla/5.0 (X11; Ubuntu; Linux x86_64; rv:61.0) Gecko/20100101 Firefox/61.0"
      } else if (sUsrAg.indexOf("Opera") > -1) {
        sBrowser = "Opera";
      } else if (sUsrAg.indexOf("Trident") > -1) {
        sBrowser = "Microsoft Internet Explorer";
        //"Mozilla/5.0 (Windows NT 10.0; WOW64; Trident/7.0; .NET4.0C; .NET4.0E; Zoom 3.6.0; wbx 1.0.0; rv:11.0) like Gecko"
      } else if (sUsrAg.indexOf("Edge") > -1) {
        sBrowser = "Microsoft Edge";
        //"Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/58.0.3029.110 Safari/537.36 Edge/16.16299"
      } else if (sUsrAg.indexOf("Chrome") > -1) {
        sBrowser = "Google Chrome or Chromium";
        //"Mozilla/5.0 (X11; Linux x86_64) AppleWebKit/537.36 (KHTML, like Gecko) Ubuntu Chromium/66.0.3359.181 Chrome/66.0.3359.181 Safari/537.36"
      } else if (sUsrAg.indexOf("Safari") > -1) {
        sBrowser = "Apple Safari";
        //"Mozilla/5.0 (iPhone; CPU iPhone OS 11_4 like Mac OS X) AppleWebKit/605.1.15 (KHTML, like Gecko) Version/11.0 Mobile/15E148 Safari/604.1 980x1306"
      } else {
        sBrowser = "unknown";
      }
      return sBrowser
    }
  },
  methods: {
    dismissPrompt() {
      this.installed = true
    },

    installPWA() {
      console.log(444, this.installEvent)
      this.installEvent.prompt()
      this.installEvent.userChoice.then((choice) => {
        this.dismissPrompt() // Hide the prompt once the user's clicked
        if (choice.outcome === 'accepted') {
          // Do something additional if the user chose to install
          this.$router.push('/documents')
        }
      })
    },

  }
}
</script>

<style scoped>
.install {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100vw;
}
</style>
